import React from 'react';
import { connect } from 'react-redux';
import { slugify, getDefaultVoted } from '../../../../services/functions';
import { Link } from 'react-router-dom';
import {SingleSkeleton,ParagraphSkeleton} from '../../SkeletonBar';
import { cutWord } from '../../../../services/functions';
import { setBodyArticle, setSectionId, setCategoryId, setTitleArticle, setArticleId, setVoted} from '../../../../actions'

const ListLink = ({ title, links, categoryId }) => {
    return (
        links ?
            <div className='col--m4 lg-pd-top'>
                <h5 className='text-18 text-black70 no-mg text-normal bs-mg-half-bottom'>
                    { title }
                </h5>
                    {
                        links.articles.slice(0,5).map( (link, index) => {
                            const maxLength = 4
                            const isArticle = maxLength > 4 && index === maxLength - 1
                            return (
                                <p>
                                    <Link onClick={() => {
                                                    this.props.setTitleArticle(link.name)
                                                    this.props.setBodyArticle(link.body)
                                                    this.props.setVoted(getDefaultVoted(link.id))
                                        }}
                                            className='text-14' to={ index < maxLength ? `/articles/${slugify(link.title,link.id)}` : `/categories/${links.category_id}`} className='text-14'>
                                        { index < maxLength ? cutWord({max: 28, min: 26}, link.title, 34) : '...' }
                                    </Link>
                                </p>
                            )
                        })
                    }
            </div> :
            <div className='col--m4 lg-pd-top'>
                <SingleSkeleton className='lg-mg-bottom' darken/>
                <ParagraphSkeleton />
            </div>
        )
        
            
}



const mapStateToProps = (state) => {
    return { articles : state.article }
};
      
const mapDispatchToProps = (dispatch) => ({
    setBodyArticle: (payload) => dispatch(setBodyArticle(payload)),
    setSectionId: (payload) => dispatch(setSectionId(payload)),
    setCategoryId: (payload) => dispatch(setCategoryId(payload)),
    setTitleArticle: (payload) => dispatch(setTitleArticle(payload)),
    setArticleId: (payload) => dispatch(setArticleId(payload)),
    setVoted: (payload) => dispatch(setVoted(payload))
})
    
export default connect(mapStateToProps, mapDispatchToProps)(ListLink);


