import React from 'react';
import { connect } from 'react-redux';

// Component Import Header
import HeaderHome from '../../components/layouts/Header/';
import HeroHome from '../../components/commons/Hero/HeroHome';
import ListOptionCategory from '../../components/commons/Lists/ListOptionCategory';
import ListTips from '../../components/commons/Lists/ListTips';

// Component Import Section
import SectionHomeFirst from '../../components/commons/Sections/Section-Home-1'
import SectionHomeSecond from '../../components/commons/Sections/Section-Home-2';
import SectionHomeThird from '../../components/commons/Sections/Section-Home-3';
import SEO from '../../components/layouts/SEO';


import { defaultCode } from '../../constants/globalVariable';
import { sortedArticle, loadAllData } from '../../services/functions';
import { setAllArticle, setAllSection } from '../../actions';
import { getAllArticles, getAllSection, checkOnline } from '../../services/api';


const dataCategories = [
    {
        'img': require('../../assets/help-1.svg'),
        'title': 'Panduan Umum',
        'subtitle': 'Cari tau bagaimana beraktivitas di kitabisa.com',
        'id': 360000012732
    },
    {
        'img': require('../../assets/help-1.svg'),
        'title': 'Donatur',
        'subtitle': 'Jadilah donatur yang padai tidak mudah terperdaya',
        'id': 360000012692
    },
    {
        'img': require('../../assets/help-1.svg'),
        'title': 'Campaigner',
        'subtitle': 'Seberapa mudah membuat campaign, cari tau disini',
        'id': 360000012712
    },
]

class HomePage extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            showMenu: false,
            sortedArticle: {}
        }

        this.handlerOnline = this.handlerOnline.bind(this);
        this.handlerOffline = this.handlerOffline.bind(this)
    }

    async handlerOnline(online){
        console.log('jalan check online')
        if (!this.props.article.get('articles').size)
            await loadAllData(getAllArticles,'articles')( (data) => {
                localStorage.setItem('articles', JSON.stringify(data))
                this.props.setAllArticle(data)});
    
        if (!this.props.article.get('sections').size)
            await loadAllData(getAllSection,'sections')(data => {
                localStorage.setItem('sections', JSON.stringify(data))
                this.props.setAllSection(data)
            });
    }

    async handlerOffline(){
        this.props.setAllArticle(JSON.parse(localStorage.getItem('articles')))
        this.props.setAllSection(JSON.parse(localStorage.getItem('sections')))
    }

    async componentDidMount(){
        await checkOnline({
            resolve: this.handlerOnline,
            reject: this.handlerOffline
        })

        
        await sortedArticle({
            sortedID: defaultCode, 
            data: Object.values(this.props.article.toJS().articles), 
            done: (newState) => { 
                const { sections } = this.props.article.toJS()
                for(let key in newState){
                    let index = Object.values(sections).find(value => Number(value.id) === Number(newState[key].id));
                    newState[key].category_id = index.category_id
                }
                this.setState({sortedArticle: newState})
                console.log('baru')
            }
        });
    }

    render(){
        return (
            <div>
                <SEO
                    schema={'http://127.0.0.1:3000'}
                    title={`Kitabisa Help Center`}
                    description={`Pusat bantuan - Cari seputar Artikel yang dapat membantu anda`}
                    path={`http://127.0.0.1:3000`}
                    contentType={`text/html; charset=utf-8`}
                    category={'Home'}
                    tags={`Home`}
                />
                <main>
                    <HeroHome />
                    <section className='section no-border no-pd-bottom'>
                        <div className='container container--large'>
                            <ListOptionCategory categories={dataCategories}/>
                        </div>
                    </section>
                    <section className='no-border'>
                        <div className='container container--large bg-white bs-pd-top sp-pd-bottom br-4'>
                            <SectionHomeFirst title={'Suggested topics'} />
                            <SectionHomeSecond sortedArticle={this.state.sortedArticle}/>
                            <SectionHomeThird sortedArticle={this.state.sortedArticle}/>
                        </div>
                    </section>
                    <section className='no-border'>
                        <div className='container container--large bg-white bs-pd-top sp-pd-bottom br-4'>
                            <SectionHomeFirst title={'Tips Campaigner'} />
                            <div className='container'>
                                <ListTips />
                            </div>
                        </div>
  
                    </section>

                </main>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return { article : state.article }
};
  
const mapDispatchToProps = (dispatch) => ({
    setAllArticle: (payload) => dispatch(setAllArticle(payload)),
    setAllSection: (payload) => dispatch(setAllSection(payload))
})

export default connect(mapStateToProps, mapDispatchToProps)(HomePage);


