import React from 'react';

import { InputButton, InputWrapperWithIcon, InputSearch } from '../style';

const SearchInput = ({type, value, name, onChange, placeholder, resetValue, className, size, bordered, handleSubmit}) => (
    <InputWrapperWithIcon className={className} search>
        <form onSubmit={(e) => {
            e.preventDefault();
            handleSubmit(value);
        }}>
            <InputSearch 
                className="form-input"
                name={name}
                type={type}
                value={value}
                onChange={onChange}
                placeholder={placeholder} 
                size={size}
                bordered={bordered} />
            <InputButton className={'material-icons'} prefix>search</InputButton>
            { value.length > 0 && 
                <InputButton className={'material-icons'} 
                            suffix 
                            type={'closeIcon'}
                            onClick={() => resetValue()}>
                            close
                </InputButton> }
        </form>
    </InputWrapperWithIcon>
)

export default SearchInput


