import styled from 'styled-components';
import { sc_color } from '../../utils';
import { media } from '../../utils/responsive-style';

export const InputWrapper = styled.div `
    position: relative;
    font-size: 16px;
    display: inline-block;
`

export const InputWrapperWithIcon = InputWrapper.extend `
    ${media.phone `
        width: 100%;
    `}
`

export const InputButton = styled.span `
    position: absolute;
    top: 50%;
    transform: translate(0%, -50%);
    color: #4D4D4D;
    font-size: 20px;

    left: ${props => props.prefix ? '0.7em' : 'auto'};
    right: ${props => props.suffix ? '0.7em' :'auto'};

    &.close-icon {
    }

    ${props => {
        if (props.type === 'closeIcon') {
           return  `
           font-size: 16px;
           color: ${sc_color.mediumGray} ;
           cursor: pointer;
           padding: 4px;
              
            &:hover {
                font-size: 16px;
                background-color: ${sc_color.mediumGray};
                border-radius: 100%;
                color: white;
            }
            `
        }
    }}

`

export const Input = styled.input `
    background-color: #fff;
    -webkit-appearance:none ;
    border-radius: 4px;
    color: #606266;
    box-sizing: border-box;
    font-size: ${props => props.size === 'large' ? '16px' : '14px'};
    display: inline-block;
    font-weight: normal;
    padding: 0 1.5em;

    text-align: left;
    transition: all 0.9s;
    max-width: 100%;

    &:focus {
        border: 1px ${sc_color.colorBrand};
        outline: 0;
    }
    :-webkit-autofill {
        background: #fff !important;
    }
`


const inputSize = {
    'large' : `
        height: 45px;
        width: auto;
    `,
    'hero' : `
        height: 55px;
        width: 496px;
    `,
    'default': `
        height: 40px;
        width: 496px; 
    `
};

export const InputSearch = Input.extend `
    outline: none;
    transition: border-color .2s cubic-bezier(.645,.045,.355,1);
    font-weight: normal;
    padding-left: 3em;
    ${props => props.size ? inputSize[props.size] : inputSize['default'] }

    border: ${props => props.bordered  ? `1px solid #ccd0d3` : `0` };
    
    ${media.phone`
        width: 100%;
    `}

    ::placeholder {
        color: #E0E0E0;
        transition: color .2s ease;
    }

    &:focus {
        ::placeholder {
            color: #A5A5A5;
        }
        border: ${props => props.bordered ? `1px solid #00aeef` : `0` };
    }
`


