import styled from 'styled-components';
import { media } from '../../../utils/responsive-style';

export const ListOptionItem = styled.div `
    flex: 1;
    flex-grow: 1;
    flex-shrink: 0;
    flex-basis: 400px;
    flex-wrap: wrap;
    background-color: white;
    margin: 1.2em;
    transition: all 250ms;
    padding: 4em 2em 3em 2em;
    text-align: center;
    border-radius: 4px;

    &:first-child {
        margin-left: 0;
    }

    &:last-child {
        margin-right: 0;
    }
    flex-basis: 30%;

    :nth-child(3){
        margin-right: 0;
    }

    h3 {
        font-size: 20px;
        margin:0;
        margin-top: 1em;
        font-weight: normal;
    }

    p {
        font-size: 12px;
        margin: 0;
    }

    ${media.phone`
        margin: 0;
        margin-bottom: 1em;
        flex-basis: 100%;
    `}

`


