import React from 'react';

const ListData = [
    {
        title: '1. Pelajari Campaign',
        description: 'Cara bikin campaign dan kenapa campaign bisa sukses',
        srcImg: '../../../../assets/help-1.svg'
    },
    {
        title: '2. Lengkapi Verifikasi',
        description: 'Lihat dengan detail tahapan verifikasi untuk keamaan',
        srcImg: '../../../../assets/help-1.svg'
    },
    {
        title: '3. Lihat Kitabisa',
        description: 'Apa itu Kitabisa',
        srcImg: '../../../../assets/help-1.svg'
    },
]
const ListTipsItem = ({srcImg, title, description}) => (
    <div className='col--m4 lg-pd-top align-center'>
        <img src={require('../../../../assets/help-1.svg')} alt="Ilus Icons" className='bs-mg-bottom' />
        <a href="">
            <h3 className='no-mg text-20 text-normal'>
                {title}
            </h3>
        </a>
        <p className='no-mg-bottom text-14'> {description} </p>
    </div>
);

const ListTipsWrapper = (props) => (
    <div className='col lg-pd-lr'>
        {
            ListData.map(property => <ListTipsItem title={property.title} srcImg={property.srcImg} description={property.description}/>)
        }
    </div>
)

export default ListTipsWrapper;




