import React from 'react';
import { withRouter } from 'react-router';
// import components 
import { HeroContainerWithPadding, 
         HeroContainerBackground, 
         HeroTitle,
         HeroTextContainerCenter } from './style';

import InputSearch from '../../Inputs/InputSearch';
import { sc_color } from '../../../utils';
import { slugify } from '../../../../services/functions';
import queryString from 'query-string';


import Responsive from 'react-responsive-decorator';

class HeroHome extends React.Component {
    constructor(props){
        super(props)
        this.state = {
            searchInput: '',
            isMobile: false
        }

        this._handleSubmit = this._handleSubmit.bind(this);
    }

    componentDidMount(){
        this.props.media({ minWidth: 768 }, () => {
            this.setState({
              isMobile: false
            });
          });
      this.props.media({ maxWidth: 768 }, () => {
            this.setState({
              isMobile: true
            });
          });
    }

    _handleChange(value){
        let newState = Object.assign({}, ... this.state, {searchInput: value })
        this.setState(newState);
    }

    _handleSubmit(value) {
        console.log('tadaaa', this.props);
        this.props.history.push(`/articles/search?${queryString.stringify({q: value})}`);
    }
    render(){
        let { isMobile } = this.state;

        return (
            <HeroContainerWithPadding>
                <HeroContainerBackground height={278} background={sc_color.darkBlue}>
                    <HeroTextContainerCenter className={'container'}>
                        <HeroTitle>Halo, ada yang bisa kami bantu?</HeroTitle>
                        <InputSearch 
                            type={'search'} 
                            placeholder={isMobile ? 'Cari ...' : 'Search ( Cari semua informasi yang kamu inginkan)'}
                            value={this.state.searchInput}
                            onChange={(e) => this._handleChange(e.target.value)}
                            resetValue={() => this._handleChange('')}
                            size={'hero'}
                            bordered={false}
                            handleSubmit={this._handleSubmit}
                            />  
                    </HeroTextContainerCenter>
                </HeroContainerBackground>
            </HeroContainerWithPadding>
    
        )
    }
}

export default withRouter(Responsive(HeroHome));


