import React from 'react';
import Border from '../../Border';

const SectionHomeTitle = ({title}) => (
    <div className='container container--in-mobile-medium'>
            <h3 className='text-20 text-semibold margin-left-title text-black70'> 
                {title}
            </h3>
            <Border color={'#e7e9eb'}/>
    </div>
)

export default SectionHomeTitle;


