import styled from 'styled-components';
import { media } from '../../../utils/responsive-style';

export const HeroContainer = styled.section `
    margin: 0;
    padding: 0;
    position: relative;
    font-weight: 600;
    border: 0px;
`

export const HeroContainerWithPadding = HeroContainer.extend `
    padding-top: 70px;
`

export const HeroContainerBackground = styled.div `
    margin: 0;
    height: ${props => props.height || 580}px;
    position: relative;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background: ${props => props.background || 'gray'}
`

export const HeroTextContainer = styled.div `
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    h3,
    p {
    margin: 0;
    }

    h3 {
    margin-bottom: 0.4em;
    
    }

    p {
    line-height: 31px;
    }

    a {
    margin-top: 3em;
    }

    ${media.phone`
        text-align: center;
        width: 90% !important;
        p {
            width: 100%;
        }

        h3 {
            font-size: 24px;
        }

        .btn {
            width: 100%;
        }
    `}
`

export const HeroTextContainerCenter = HeroTextContainer.extend `
    color: #fff;
    text-align: center;

    h2,
    p {
    margin: 0 auto;

    h3 {
        margin-bottom: 0.1em;
        font-size: 36px;
    }

    ${media.phone `
        #title {
            font-size: 24px;
        }

        h3 {
            font-size: 24px;
        }
    `}
`

export const HeroTitle = styled.h3 `
    font-weight: 600;
    font-size: 36px;
    margin: 0;
`



