import React from 'react';
import ListLink from '../../Lists/ListLink';
import { defaultCode } from '../../../../constants/globalVariable';

const SectionHomeThird = ({ sortedArticle }) => (
    <div className='container container--in-mobile-medium'>
        <div className='col lg-pd-lr mobile-no-padding'>
            {
                defaultCode.map((value,index) => (
                            index > 2 && <ListLink title={value.title} categoryId={value.category_id} links={sortedArticle[String(value.id)]}/>
                ))
            }
        </div>
    </div>
)

export default SectionHomeThird;


