import React from 'react';
import { ListOptionItem } from './style';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { setCategoryId } from '../../../../actions'

const ListOptionCategory = ({ categories, setCategoryId }) => (
    <div className={'flex-container'}>
        { categories.map((category, index) => (
            <ListOptionItem key={index}>
                <img src={category.img} alt={`illus-${index}`}/>
                <Link onClick={() => setCategoryId(category.id)} to={`/categories/${category.id}`}>
                    <h3> { category.title} </h3>
                </Link>
                <p> { category.subtitle }</p>
            </ListOptionItem>
        ))}
    </div>
)

const mapStateToProps = (state) => {
    return { articles : state.article }
};
      
const mapDispatchToProps = (dispatch) => ({
    setCategoryId: (payload) => dispatch(setCategoryId(payload)),
})
    
export default connect(mapStateToProps, mapDispatchToProps)(ListOptionCategory);


