import styled from 'styled-components';

const Border = styled.hr.attrs({
    color: props => props.color || '#E0E1E1'
})`
    box-sizing: content-box;
    height: 0px;
    overflow: visible;
    border-top: 1px solid ${props => props.color};
    display: block;
    border-bottom: 0;
`

export default Border 


