import React from 'react'
import Helmet from 'react-helmet';


// itemtype contoh localhost:3000/categories
// contenttype contoh text/html; charset=utf-8
//

const getMetaTags = ({
    title, description, url, contentType, category, tags, 
}) => {
    const metaTags = [
        // For Google Plus
        { itemprop: 'name', content: title },
        { itemprop: 'description', content: description },
        { itemprop: 'image', content: 'https://lh4.googleusercontent.com/-VZJRgemyikI/VL5dasWzBRI/AAAAAAAAABE/PVo9UcCuTpU/w555-h553-no/logo%2Bkitabisa.png' },
        // For Web
        { name: 'description', content: description },
        // For Twiiter card Dara
        { name: 'twitter:card', content: 'summary_large_image' },
        { name: 'twitter:site', content: '@kitabisacom' },
        { name: 'twitter:title', content: `${title} | Kitabisa.com - Indonesia's Fundraising Platform` },
        { name: 'twitter:description', content: description },
        { name: 'twitter:creator', content: '@kitabisacom' },
        { name: 'twitter:image:src', content: `https://lh4.googleusercontent.com/-VZJRgemyikI/VL5dasWzBRI/AAAAAAAAABE/PVo9UcCuTpU/w555-h553-no/logo%2Bkitabisa.png` },
        // For Open Graph data
        { name: 'og:title', content: `${title} | Kitabisa.com - Indonesia's Fundraising Platform` },
        { name: 'og:type', content: contentType || ' text/html; charset=utf-8'},
        { name: 'og:url', content: url },
        { name: 'og:image', content: 'https://lh4.googleusercontent.com/-VZJRgemyikI/VL5dasWzBRI/AAAAAAAAABE/PVo9UcCuTpU/w555-h553-no/logo%2Bkitabisa.png'},
        { name: 'og:description', content: description },
        { name: 'og:site_name', content: `Kitabisa.com - Indonesia's Fundraising Platform` },
      ];

      if (category) metaTags.push({ name: 'article:section', content: category });
      return metaTags;
}

const SEO = ({
    schema, title, description, path, contentType, category, tags
}) => (
    <Helmet
        htmlAttributes={{
            lang:'en',
            itemscope: undefined,
            itemType: schema
        }}
        title={title}
        link={[
            { rel: 'canonical', href: schema}
        ]}
        meta={getMetaTags({
            title,
            description,
            url: path, 
            category,
            tags, 
            contentType
        })}
    />
);

export default SEO;


